body{
    background-color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    
}
.profile_row1{
    display: flex;
    flex-direction: row;
    background-color: white;
    border-bottom: 1px solid green;
    padding-left: 10%;
    padding-right: 10%;
    justify-content: space-between;
    padding-top: 10px;
    padding-bottom: 10px;
}
.profile_row1_img{
    width: 4%;
}
.profile_row1_text{
    
    width: 45%;
    padding-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.profile_row2{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    padding: 5%;
}
.profile_row2_img{
    width: 30%;
    border-bottom-left-radius:20px;
    border-top-left-radius: 20px;

}
.profile_row2_inner1{
    background-color: rgb(224, 224, 224);
    display: flex;
    flex-direction: row;
    width: 70%;
    border-radius: 20px;
   
}
.profile_row2_inner_text{
   
    padding: 3%;
    width: 97%;
    border-bottom-right-radius:20px;
    border-top-right-radius: 20px;
}
.profile_row2_inner_text_label1{
    font-weight: bold;
    font-size: xx-large;
}
.profile_row2_inner_text_label2{
    color: gray;
    font-size: small;
}
.profile_row2_inner_text_label3{
    color: blue;
    padding-top:20px ;
    padding-bottom: 10px;
    
}
.profile_row2_inner_text_label3_ul{
    display: flex;
    flex-direction: row;

   padding-right: 15%;
    justify-content: space-between;
}
.profile_row2_inner_text_label3_li,.profile_row2_inner_text_label3_li1{
    display: flex;
    flex-direction: column;
}
.profile_row2_inner2{
    background-color: rgb(226, 226, 226);
    width: 70%;
    border-radius: 20px;
  
    
}
.profile_row2_inner2_text{
    display: flex;
    flex-direction: column;
    padding: 3%;
    width: 97%;
}
.profile_row2_inner2_text_label{
    font-weight: bold;
    font-size: xx-large;
}

.profile_row2_inner_ul{
    display: flex;
    flex-direction: row;
}