body{
    background-color: white;
   
   
}
.Login_inner{
    background-color: rgba(0, 0, 0, 0.253);;
    padding: 30%;
   


}
.Login_inner1{
    background-color: white;
    padding: 30px 20px;
    border-radius: 5px;
    border: 1px solid black;
   
   
}
.Login_inner_row1{
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid grey;
}
.Login_inner_row1 label{
    font-size: 1.4rem;
    width: 100%;
    padding-bottom: 10px;
}
.Login_inner_row1 button{
    justify-content: right;
    border: none;
}
.Login_inner_row1 button:hover{
    color: rgb(194, 194, 194);


}
.Login_inner_row2,.Login_inner_row3{
    display: flex;
    flex-direction: column;
    padding-top: 5px;
    padding-bottom: 5px;
}
.Login_inner_row2 input,.Login_inner_row3 input{
    padding-top: 5px;
    padding-bottom: 5px;
}
.Login_inner_row4{
    padding-top: 5px;
    padding-bottom: 5px;
}
.Login_inner_row5{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 5px;
    border-bottom: 1px solid gray;
   
}
.Login_inner_row5 button{
    width: 100%;
    border: none;
    border-radius: 20px;
    padding: 10px 20px;
    background-color: orange;
    color: white;
    font-size: 1.4rem;
}
.Login_inner_row5 button:hover{
    background-color: rgb(206, 144, 30);
}
