body{
    background-color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding: 0;
    
    
 }
 .Register_row1{
     background-color: white;
    
     display: flex;
     flex-direction: row;
     padding-bottom: 10px;
     padding-top: 10px;
     padding-left: 5%;
     border-bottom: solid gray;
 }
 .Register_row1_text{
     display: flex;
     flex-direction: column;
     margin-left: 10px;
 }
 .Register_row1_text_label{
     font-size: 1.4rem;
 }
 .Register_row2{
     text-align: right;
     padding-top: 8%;
     padding-left: 8%;
     padding-right: 8%;
     background-color: rgb(236, 236, 236);
  
 }
 .Register_row2_inner{
     background-color: white;
     display: flex;
     flex-direction: row;
    
 }
 .Register_row2_inner_left{
     width: 45%;
     background-color: rgb(185, 185, 185);
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     padding: 5%;
 }
 .Register_row2_inner_right{
     background-color: white;
     width: 50%;
     padding:20px ;
    
 }
.Register_row2_inner_right_box{
  
  display: flex;
  flex-direction: column;
 
}
.Register_row2_inner_right_box_row{
    display: flex;
    flex-direction: row;
    margin-bottom: 15px
}
.Register_row2_inner_right_box_row label{
    width: 50%;
    text-align: left;
}
.Register_row2_inner_right_box_row input{
    width: 50%;
    border: none;
    outline: none;
    padding-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid gray;
} 
 .Register_row2_inner_right_label1{
    padding-bottom: 20px;
    text-align: left;
    font-size: large;
 }
 .Register_row2_inner_right button{
     color: white;
     background-color: orangered;
     border: none;
     border-radius: 20px;
     padding: 10px 20px;
     margin-top: 20px;
 }
 .Register_row2_inner_right button:hover{
     background-color: rgb(216, 91, 46);
 }
 .Register_row2_label{
     font-size: 1.4rem;
 }
 .Register_row2_label1{
     font-weight: bold;
     font-size:xx-large;
 }
 .Register_row3{
     text-align: center;
     background-color: white;
    
 }
 