body{
    
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.Home_row1{
    background-color: red;
    display: flex;
    flex-direction: column;
    padding: 100px 15%;
}
.Home_row1_inner_row1{
    display: flex;
    flex-direction: row;
    background-color: white;
    justify-content: space-between;
    padding: 5px;
    border-radius: 5px;
    background-color: white;
}
.Home_row1_inner_row1_img{
    width: 5%;
}
.Home_row1_inner_row1_text{
    background-color: white;
    width: 50%;
    padding-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.Home_row1_inner_row2{
    display: flex;
    flex-direction: row;
    padding-top: 80px;
    

}
.Home_row1_inner_row2 input{
    width: 100%;
    
    outline: none;
}
.Home_row1_inner_row2 button{
    background-color: rgb(250, 85, 85);
    padding: 20px 30px;
    border: none;
    color: white;
}
.Home_row1_inner_row2 button:hover{
    background-color: rgb(204, 67, 67);
}
.Home_row2{
    background-color: rgb(180, 177, 177);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 100px 15%;
}
.Home_row2_inner1{
    background-color: white;
    display: flex;
    flex-direction: row;
    border-radius: 20px;
    margin-bottom: 50px;
}
.Home_row2_inner1_img{
    width: 25%;
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
}
.Home_row2_inner_text{
   background-color: white;
    width: 100%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
}
.Home_row2_inner_text_label{
    font-weight: bold;
    font-size: xx-large;
    
}
.Home_row2_inner_text_label1{
    display: flex;
    flex-direction: row;
    
}
.Home_row2_inner_text_col{
    background-color: white;
    width: 50%;
    display: flex;
    flex-direction: row;
    gap: 30px;
}
.Home_row2_inner_text_col1,.Home_row2_inner_text_col2{
    display: flex;
    flex-direction: column;
}
.popup{
    display: none;
}
.popup:hover{
    display: block;
}