body{
    margin: 0%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.home_row1{
    background-color: yellow;
    display: flex;
    flex-direction: row;
    width: 100%;
    position: fixed;
}
.home_row1_left{
    background-color: white;
    width: 45%;
    padding-left: 5%;
    display: flex;
    flex-direction: row;
    padding-bottom: 10px;
    padding-top: 10px;
}
.home_row1_right{
    background-color: white;
    width: 45%;
    padding-right: 5%;
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;
}
.home_row1_left_text{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}
.home_row1_left_text_label{
    font-size: 1.4rem;
}
.home_row1_right button{
    background-color: rgb(68, 124, 138);
    color: white;
    border: none;
    border-radius: 20px;
    padding: 10px 20px;
    margin-left: 10px;
    font-size: medium;
}
.home_row1_right button:hover{
    background-color: rgb(38, 85, 97);
}
.home_row3{
    background-color: white;
}
.home_row3 ul{
    background-color: white(56, 179, 179);
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 20px 5%;
    justify-content: space-between;
}
.home_row3 li{
    background-color: white;
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 18%;

}
.home_row2{
    background-color: red;
    padding: 150px 20%;
    background-attachment: fixed;
}
.home_row2_inner{
    background-color: rgba(0, 0, 0, 0.308);
    padding: 80px 5%;
    display: flex;
    flex-direction: column;
}
.home_row2_inner_row3{
    background-color: white;
    display: flex;
    flex-direction: row;
    border-radius: 10px;
    
}
.home_row2_inner_row3_co11.home_row2_inner_row3_co12.home_row2_inner_row3_co13{
    width: 25%;
    padding-top: 10px;
    padding-bottom: 10px;
}
.home_row2_inner_row3 button{
    width: 25%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border: none;
    color: white;
    background-color: rgb(32, 174, 199);
}
.home_row2_inner_row3 button:hover{
    background-color: rgb(54, 96, 104);
}
.home_row2_inner_row3 input,.home_row2_inner_row3 select{
    width: 100%;
    box-sizing: border-box;
    border: none;
    outline: none;
    padding-top: 5px;
    padding-bottom: 5px;
}
.home_row2_inner_row4{
    text-align: right;
    color: white;
}
.home_row2_inner_row3_co12{
    border-left: 1px solid grey;
    border-right: 1px solid grey;
}
.home_row2_inner_row3_co11{
    padding-left: 10px;
}
.home_row2_inner_row4_l2,.home_row2_inner_row4_l4{
    color: blue;
}
.home_row2_inner_row1{
    color: white;
    font-size: 1.4rem;
}
.home_row2_inner_row2{
    color: white;
    font-size: large;
    padding-bottom: 5px;
}
.home_row2_inner_row4{
    padding-top: 10px;
}